import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { Helmet } from 'react-helmet-async';
import {
    Alert,
    Avatar,
    Box,
    Button,
    Card,
    CardContent,
    Chip,
    Container,
    Dialog,
    DialogActions,
    DialogContent,
    DialogTitle,
    FormControl,
    Grid,
    IconButton,
    InputAdornment,
    InputLabel,
    MenuItem,
    Paper,
    Select,
    Snackbar,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    TextField,
    Tooltip,
    Typography,
    Tabs,
    Tab,
    List,
    ListItem,
    ListItemIcon,
    ListItemText,
    Divider,
    LinearProgress
} from '@mui/material';
import {
    Cancel as RejectIcon,
    CheckCircle as ApproveIcon,
    Delete as DeleteIcon,
    Edit as EditIcon,
    Person as PersonIcon,
    Search as SearchIcon,
    Visibility as ViewIcon,
    Group as CollaborationIcon,
    TrendingUp as AnalyticsIcon,
    Code as CodeIcon,
    Description as DescriptionIcon,
    Star as StarIcon,
    MonetizationOn as RevenueIcon,
    PlayArrow as RunIcon,
    Timer as TimerIcon,
    Memory as MemoryIcon,
    Storage as StorageIcon,
    Speed as SpeedIcon
} from '@mui/icons-material';
import { formatDistanceToNow } from 'date-fns';
import { useAuth } from 'src/contexts/AuthContext';
import { projectService } from 'src/services/projectService';

const TabPanel = ({ children, value, index, ...other }) => (
    <div
        role="tabpanel"
        hidden={value !== index}
        id={`admin-tabpanel-${index}`}
        aria-labelledby={`admin-tab-${index}`}
        {...other}
    >
        {value === index && <Box sx={{ p: 3 }}>{children}</Box>}
    </div>
);

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
};

const AdminProjectManagement = () => {
    const { user } = useAuth();
    
    const [tabValue, setTabValue] = useState(0);
    const [projects, setProjects] = useState([]);
    const [filteredProjects, setFilteredProjects] = useState([]);
    const [loading, setLoading] = useState(true);
    const [searchTerm, setSearchTerm] = useState('');
    const [filterStatus, setFilterStatus] = useState('all');
    const [filterType, setFilterType] = useState('all');
    const [selectedProject, setSelectedProject] = useState(null);
    const [openRejectDialog, setOpenRejectDialog] = useState(false);
    const [openDeleteDialog, setOpenDeleteDialog] = useState(false);
    const [rejectReason, setRejectReason] = useState('');
    const [adminAnalytics, setAdminAnalytics] = useState(null);
    const [snackbar, setSnackbar] = useState({
        open: false,
        message: '',
        severity: 'success'
    });

    useEffect(() => {
        loadProjects();
        loadAdminAnalytics();
    }, []);

    useEffect(() => {
        filterProjects();
    }, [projects, searchTerm, filterStatus, filterType]);

    const loadProjects = async () => {
        try {
            setLoading(true);
            const response = await projectService.getAllProjectsAdmin();
            setProjects(response.data);
        } catch (error) {
            console.error('Error loading projects:', error);
            setSnackbar({
                open: true,
                message: 'Failed to load projects',
                severity: 'error'
            });
        } finally {
            setLoading(false);
        }
    };

    const loadAdminAnalytics = async () => {
        try {
            const response = await projectService.getAdminAnalytics();
            setAdminAnalytics(response.data);
        } catch (error) {
            console.error('Error loading admin analytics:', error);
        }
    };

    const filterProjects = () => {
        let filtered = [...projects];

        // Filter by search term
        if (searchTerm) {
            filtered = filtered.filter(project =>
                project.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                project.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
                project.owner.email.toLowerCase().includes(searchTerm.toLowerCase())
            );
        }

        // Filter by status
        if (filterStatus !== 'all') {
            filtered = filtered.filter(project => project.status === filterStatus);
        }

        // Filter by type
        if (filterType !== 'all') {
            filtered = filtered.filter(project => project.project_type === filterType);
        }

        setFilteredProjects(filtered);
    };

    const handleApproveProject = async (projectId) => {
        try {
            await projectService.approveProject(projectId);
            setProjects(projects.map(project => 
                project.id === projectId 
                    ? { ...project, status: 'approved', is_approved: true }
                    : project
            ));
            setSnackbar({
                open: true,
                message: 'Project approved successfully!',
                severity: 'success'
            });
            loadAdminAnalytics(); // Refresh analytics
        } catch (error) {
            console.error('Error approving project:', error);
            setSnackbar({
                open: true,
                message: 'Failed to approve project',
                severity: 'error'
            });
        }
    };

    const handleRejectProject = async (projectId) => {
        try {
            await projectService.rejectProject(projectId, rejectReason);
            setProjects(projects.map(project => 
                project.id === projectId 
                    ? { ...project, status: 'rejected' }
                    : project
            ));
            setOpenRejectDialog(false);
            setRejectReason('');
            setSnackbar({
                open: true,
                message: 'Project rejected successfully!',
                severity: 'success'
            });
            loadAdminAnalytics(); // Refresh analytics
        } catch (error) {
            console.error('Error rejecting project:', error);
            setSnackbar({
                open: true,
                message: 'Failed to reject project',
                severity: 'error'
            });
        }
    };

    const handleDeleteProject = async (projectId) => {
        try {
            await projectService.deleteProject(projectId);
            setProjects(projects.filter(project => project.id !== projectId));
            setOpenDeleteDialog(false);
            setSelectedProject(null);
            setSnackbar({
                open: true,
                message: 'Project deleted successfully!',
                severity: 'success'
            });
            loadAdminAnalytics(); // Refresh analytics
        } catch (error) {
            console.error('Error deleting project:', error);
            setSnackbar({
                open: true,
                message: 'Failed to delete project',
                severity: 'error'
            });
        }
    };

    const handleConfirmDelete = () => {
        if (selectedProject) {
            handleDeleteProject(selectedProject.id);
        }
    };

    const handleCancelDelete = () => {
        setOpenDeleteDialog(false);
        setSelectedProject(null);
    };

    const getStatusColor = (status) => {
        switch (status) {
            case 'approved':
                return 'success';
            case 'pending':
                return 'warning';
            case 'rejected':
                return 'error';
            case 'draft':
                return 'default';
            default:
                return 'default';
        }
    };

    const getProjectTypeIcon = (type) => {
        switch (type) {
            case 'code':
                return <CodeIcon fontSize="small" />;
            case 'text':
                return <DescriptionIcon fontSize="small" />;
            default:
                return <DescriptionIcon fontSize="small" />;
        }
    };

    return (
        <>
            <Helmet>
                <title>Admin Project Management - OneDollarAI</title>
            </Helmet>

            <Container maxWidth="xl">
                <Box sx={{ mt: 3, mb: 4 }}>
                    <Typography variant="h2" component="h1" gutterBottom sx={{ color: 'black' }}>
                        Admin Project Management
                    </Typography>
                    <Typography variant="body1" sx={{ color: 'black' }}>
                        Comprehensive management and review system for all projects.
                    </Typography>
                </Box>

                {/* Tabs */}
                <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 3 }}>
                    <Tabs 
                        value={tabValue} 
                        onChange={(e, newValue) => setTabValue(newValue)}
                        sx={{
                            '& .MuiTab-root': {
                                color: 'black',
                                '&.Mui-selected': {
                                    color: 'black'
                                }
                            }
                        }}
                    >
                        <Tab label="All Projects" />
                        <Tab label="Analytics Dashboard" />
                        <Tab label="Pending Reviews" />
                        <Tab label="Collaboration Management" />
                    </Tabs>
                </Box>

                {/* All Projects Tab */}
                <TabPanel value={tabValue} index={0}>
                    <Card sx={{ mb: 3 }}>
                        <CardContent>
                            <Grid container spacing={2} alignItems="center">
                                <Grid item xs={12} md={4}>
                                    <TextField
                                        fullWidth
                                        placeholder="Search projects by title, description, or owner..."
                                        value={searchTerm}
                                        onChange={(e) => setSearchTerm(e.target.value)}
                                        InputProps={{
                                            startAdornment: (
                                                <InputAdornment position="start">
                                                    <SearchIcon sx={{ color: 'black' }} />
                                                </InputAdornment>
                                            ),
                                        }}
                                        sx={{
                                            '& .MuiInputBase-root': {
                                                color: 'black',
                                                '& fieldset': { borderColor: 'black' },
                                                '&:hover fieldset': { borderColor: 'black' },
                                                '&.Mui-focused fieldset': { borderColor: 'black' }
                                            }
                                        }}
                                    />
                                </Grid>
                                <Grid item xs={12} md={2}>
                                    <FormControl fullWidth sx={{
                                        '& .MuiInputBase-root': {
                                            color: 'black',
                                            '& fieldset': { borderColor: 'black' }
                                        }
                                    }}>
                                        <InputLabel>Status</InputLabel>
                                        <Select
                                            value={filterStatus}
                                            onChange={(e) => setFilterStatus(e.target.value)}
                                            label="Status"
                                        >
                                            <MenuItem value="all" sx={{ color: 'black' }}>All Statuses</MenuItem>
                                            <MenuItem value="draft" sx={{ color: 'black' }}>Draft</MenuItem>
                                            <MenuItem value="pending" sx={{ color: 'black' }}>Pending Review</MenuItem>
                                            <MenuItem value="approved" sx={{ color: 'black' }}>Approved</MenuItem>
                                            <MenuItem value="rejected" sx={{ color: 'black' }}>Rejected</MenuItem>
                                        </Select>
                                    </FormControl>
                                </Grid>
                                <Grid item xs={12} md={2}>
                                    <FormControl fullWidth sx={{
                                        '& .MuiInputBase-root': {
                                            color: 'black',
                                            '& fieldset': { borderColor: 'black' }
                                        }
                                    }}>
                                        <InputLabel>Type</InputLabel>
                                        <Select
                                            value={filterType}
                                            onChange={(e) => setFilterType(e.target.value)}
                                            label="Type"
                                        >
                                            <MenuItem value="all" sx={{ color: 'black' }}>All Types</MenuItem>
                                            <MenuItem value="code" sx={{ color: 'black' }}>Code Projects</MenuItem>
                                            <MenuItem value="text" sx={{ color: 'black' }}>Text Projects</MenuItem>
                                        </Select>
                                    </FormControl>
                                </Grid>
                                <Grid item xs={12} md={4}>
                                    <Typography variant="body2" sx={{ color: 'black' }}>
                                        Total: {filteredProjects.length} projects
                                    </Typography>
                                </Grid>
                            </Grid>
                        </CardContent>
                    </Card>

                    <TableContainer component={Paper} sx={{ 
                        backgroundColor: '#fff',
                        '& .MuiTableHead-root .MuiTableCell-root': {
                            backgroundColor: '#f5f5f5',
                            color: 'black',
                            fontWeight: 600
                        },
                        '& .MuiTableBody-root .MuiTableCell-root': {
                            color: 'black'
                        }
                    }}>
                        <Table>
                            <TableHead>
                                <TableRow>
                                    <TableCell>Project</TableCell>
                                    <TableCell>Owner</TableCell>
                                    <TableCell>Type</TableCell>
                                    <TableCell>Status</TableCell>
                                    <TableCell>Price</TableCell>
                                    <TableCell>Analytics</TableCell>
                                    <TableCell>Created</TableCell>
                                    <TableCell>Actions</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {filteredProjects.map((project) => (
                                    <TableRow key={project.id} sx={{ '&:hover': { backgroundColor: '#f8f9fa' } }}>
                                        <TableCell>
                                            <Box>
                                                <Typography variant="subtitle2" noWrap sx={{ color: 'black' }}>
                                                    {project.title}
                                                </Typography>
                                                <Typography 
                                                    variant="caption" 
                                                    sx={{ color: 'black' }}
                                                    noWrap
                                                >
                                                    {project.description.substring(0, 50)}...
                                                </Typography>
                                            </Box>
                                        </TableCell>
                                        <TableCell>
                                            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                                                <Avatar sx={{ width: 32, height: 32 }}>
                                                    {project.owner.avatar ? (
                                                        <img 
                                                            src={project.owner.avatar} 
                                                            alt={project.owner.first_name} 
                                                        />
                                                    ) : (
                                                        <PersonIcon fontSize="small" sx={{ color: 'black' }} />
                                                    )}
                                                </Avatar>
                                                <Box>
                                                    <Typography variant="body2" sx={{ color: 'black' }}>
                                                        {project.owner.first_name} {project.owner.last_name}
                                                    </Typography>
                                                    <Typography variant="caption" sx={{ color: 'black' }}>
                                                        {project.owner.email}
                                                    </Typography>
                                                </Box>
                                            </Box>
                                        </TableCell>
                                        <TableCell>
                                            <Chip
                                                label={project.project_type}
                                                size="small"
                                                icon={getProjectTypeIcon(project.project_type)}
                                                sx={{ color: 'black' }}
                                            />
                                        </TableCell>
                                        <TableCell>
                                            <Chip
                                                label={project.status}
                                                color={getStatusColor(project.status)}
                                                size="small"
                                            />
                                        </TableCell>
                                        <TableCell>
                                            <Typography variant="body2" sx={{ color: 'black' }}>
                                                ${project.price}
                                            </Typography>
                                            <Typography variant="caption" sx={{ color: 'black' }}>
                                                ${project.price_per_use}/use
                                            </Typography>
                                        </TableCell>
                                        <TableCell>
                                            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 0.5 }}>
                                                <Chip
                                                    icon={<ViewIcon />}
                                                    label={project.analytics?.total_views || 0}
                                                    size="small"
                                                    variant="outlined"
                                                    sx={{ color: 'black' }}
                                                />
                                                <Chip
                                                    icon={<RunIcon />}
                                                    label={project.analytics?.total_runs || 0}
                                                    size="small"
                                                    variant="outlined"
                                                    sx={{ color: 'black' }}
                                                />
                                                <Chip
                                                    icon={<RevenueIcon />}
                                                    label={`$${project.analytics?.total_revenue || 0}`}
                                                    size="small"
                                                    variant="outlined"
                                                    color="success"
                                                />
                                            </Box>
                                        </TableCell>
                                        <TableCell>
                                            <Typography variant="body2" sx={{ color: 'black' }}>
                                                {formatDistanceToNow(
                                                    new Date(project.created_at), 
                                                    { addSuffix: true }
                                                )}
                                            </Typography>
                                        </TableCell>
                                        <TableCell>
                                            <Box sx={{ display: 'flex', gap: 0.5 }}>
                                                <Tooltip title="View Project">
                                                    <IconButton 
                                                        size="small"
                                                        onClick={() => window.open(`/project/${project.id}`, '_blank')}
                                                    >
                                                        <ViewIcon fontSize="small" sx={{ color: 'black' }} />
                                                    </IconButton>
                                                </Tooltip>
                                                
                                                {project.status === 'pending' && (
                                                    <>
                                                        <Tooltip title="Approve Project">
                                                            <IconButton
                                                                size="small"
                                                                color="success"
                                                                onClick={() => handleApproveProject(project.id)}
                                                            >
                                                                <ApproveIcon fontSize="small" />
                                                            </IconButton>
                                                        </Tooltip>
                                                        <Tooltip title="Reject Project">
                                                            <IconButton
                                                                size="small"
                                                                color="error"
                                                                onClick={() => {
                                                                    setSelectedProject(project);
                                                                    setOpenRejectDialog(true);
                                                                }}
                                                            >
                                                                <RejectIcon fontSize="small" />
                                                            </IconButton>
                                                        </Tooltip>
                                                    </>
                                                )}
                                                
                                                <Tooltip title="Delete Project">
                                                    <IconButton
                                                        size="small"
                                                        color="error"
                                                        onClick={() => {
                                                            setSelectedProject(project);
                                                            setOpenDeleteDialog(true);
                                                        }}
                                                    >
                                                        <DeleteIcon fontSize="small" />
                                                    </IconButton>
                                                </Tooltip>
                                            </Box>
                                        </TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </TableContainer>
                </TabPanel>

                {/* Analytics Dashboard Tab */}
                <TabPanel value={tabValue} index={1}>
                    {adminAnalytics ? (
                        <Grid container spacing={3}>
                            <Grid item xs={12} md={3}>
                                <Card>
                                    <CardContent>
                                        <Typography variant="h6" sx={{ color: 'black', mb: 2 }}>
                                            Total Projects
                                        </Typography>
                                        <Typography variant="h3" sx={{ color: 'black' }}>
                                            {adminAnalytics.total_projects}
                                        </Typography>
                                    </CardContent>
                                </Card>
                            </Grid>
                            <Grid item xs={12} md={3}>
                                <Card>
                                    <CardContent>
                                        <Typography variant="h6" sx={{ color: 'black', mb: 2 }}>
                                            Approved Projects
                                        </Typography>
                                        <Typography variant="h3" sx={{ color: 'green' }}>
                                            {adminAnalytics.approved_projects}
                                        </Typography>
                                    </CardContent>
                                </Card>
                            </Grid>
                            <Grid item xs={12} md={3}>
                                <Card>
                                    <CardContent>
                                        <Typography variant="h6" sx={{ color: 'black', mb: 2 }}>
                                            Pending Review
                                        </Typography>
                                        <Typography variant="h3" sx={{ color: 'orange' }}>
                                            {adminAnalytics.pending_projects}
                                        </Typography>
                                    </CardContent>
                                </Card>
                            </Grid>
                            <Grid item xs={12} md={3}>
                                <Card>
                                    <CardContent>
                                        <Typography variant="h6" sx={{ color: 'black', mb: 2 }}>
                                            Total Revenue
                                        </Typography>
                                        <Typography variant="h3" sx={{ color: 'green' }}>
                                            ${adminAnalytics.total_revenue}
                                        </Typography>
                                    </CardContent>
                                </Card>
                            </Grid>

                            <Grid item xs={12}>
                                <Card>
                                    <CardContent>
                                        <Typography variant="h6" sx={{ color: 'black', mb: 2 }}>
                                            Top Performing Projects
                                        </Typography>
                                        <TableContainer>
                                            <Table>
                                                <TableHead>
                                                    <TableRow>
                                                        <TableCell>Project</TableCell>
                                                        <TableCell>Owner</TableCell>
                                                        <TableCell>Revenue</TableCell>
                                                        <TableCell>Runs</TableCell>
                                                        <TableCell>Views</TableCell>
                                                    </TableRow>
                                                </TableHead>
                                                <TableBody>
                                                    {adminAnalytics.top_projects.map((project) => (
                                                        <TableRow key={project.id}>
                                                            <TableCell>
                                                                <Typography variant="body2" sx={{ color: 'black' }}>
                                                                    {project.title}
                                                                </Typography>
                                                            </TableCell>
                                                            <TableCell>
                                                                <Typography variant="body2" sx={{ color: 'black' }}>
                                                                    {project.owner.first_name} {project.owner.last_name}
                                                                </Typography>
                                                            </TableCell>
                                                            <TableCell>
                                                                <Typography variant="body2" sx={{ color: 'green' }}>
                                                                    ${project.analytics?.total_revenue || 0}
                                                                </Typography>
                                                            </TableCell>
                                                            <TableCell>
                                                                <Typography variant="body2" sx={{ color: 'black' }}>
                                                                    {project.analytics?.total_runs || 0}
                                                                </Typography>
                                                            </TableCell>
                                                            <TableCell>
                                                                <Typography variant="body2" sx={{ color: 'black' }}>
                                                                    {project.analytics?.total_views || 0}
                                                                </Typography>
                                                            </TableCell>
                                                        </TableRow>
                                                    ))}
                                                </TableBody>
                                            </Table>
                                        </TableContainer>
                                    </CardContent>
                                </Card>
                            </Grid>
                        </Grid>
                    ) : (
                        <Box sx={{ textAlign: 'center', mt: 4 }}>
                            <LinearProgress sx={{ mb: 2 }} />
                            <Typography variant="h6" sx={{ color: 'black' }}>
                                Loading analytics...
                            </Typography>
                        </Box>
                    )}
                </TabPanel>

                {/* Pending Reviews Tab */}
                <TabPanel value={tabValue} index={2}>
                    <Card>
                        <CardContent>
                            <Typography variant="h6" sx={{ color: 'black', mb: 2 }}>
                                Projects Pending Review
                            </Typography>
                            {filteredProjects.filter(p => p.status === 'pending').length > 0 ? (
                                <List>
                                    {filteredProjects
                                        .filter(project => project.status === 'pending')
                                        .map((project) => (
                                            <ListItem key={project.id} divider>
                                                <ListItemIcon>
                                                    {getProjectTypeIcon(project.project_type)}
                                                </ListItemIcon>
                                                <ListItemText
                                                    primary={project.title}
                                                    secondary={
                                                        <Box>
                                                            <Typography variant="body2" sx={{ color: 'black' }}>
                                                                {project.description}
                                                            </Typography>
                                                            <Typography variant="caption" sx={{ color: 'black' }}>
                                                                by {project.owner.first_name} {project.owner.last_name}  
                                                                Created {formatDistanceToNow(new Date(project.created_at), { addSuffix: true })}
                                                            </Typography>
                                                        </Box>
                                                    }
                                                    sx={{ '& .MuiListItemText-primary': { color: 'black' } }}
                                                />
                                                <Box sx={{ display: 'flex', gap: 1 }}>
                                                    <Button
                                                        size="small"
                                                        color="success"
                                                        startIcon={<ApproveIcon />}
                                                        onClick={() => handleApproveProject(project.id)}
                                                    >
                                                        Approve
                                                    </Button>
                                                    <Button
                                                        size="small"
                                                        color="error"
                                                        startIcon={<RejectIcon />}
                                                        onClick={() => {
                                                            setSelectedProject(project);
                                                            setOpenRejectDialog(true);
                                                        }}
                                                    >
                                                        Reject
                                                    </Button>
                                                </Box>
                                            </ListItem>
                                        ))}
                                </List>
                            ) : (
                                <Typography variant="body1" sx={{ color: 'black', textAlign: 'center' }}>
                                    No projects pending review
                                </Typography>
                            )}
                        </CardContent>
                    </Card>
                </TabPanel>

                {/* Collaboration Management Tab */}
                <TabPanel value={tabValue} index={3}>
                    <Card>
                        <CardContent>
                            <Typography variant="h6" sx={{ color: 'black', mb: 2 }}>
                                Collaboration Management
                            </Typography>
                            <Typography variant="body2" sx={{ color: 'black', mb: 3 }}>
                                Monitor and manage project collaborations across the platform.
                            </Typography>
                            
                            {filteredProjects.filter(p => p.collaborators?.length > 0).length > 0 ? (
                                <List>
                                    {filteredProjects
                                        .filter(project => project.collaborators?.length > 0)
                                        .map((project) => (
                                            <ListItem key={project.id} divider>
                                                <ListItemIcon>
                                                    <CollaborationIcon sx={{ color: 'black' }} />
                                                </ListItemIcon>
                                                <ListItemText
                                                    primary={project.title}
                                                    secondary={
                                                        <Box>
                                                            <Typography variant="body2" sx={{ color: 'black' }}>
                                                                {project.collaborators.length} collaborators
                                                            </Typography>
                                                            <Typography variant="caption" sx={{ color: 'black' }}>
                                                                Owner: {project.owner.first_name} {project.owner.last_name}
                                                            </Typography>
                                                        </Box>
                                                    }
                                                    sx={{ '& .MuiListItemText-primary': { color: 'black' } }}
                                                />
                                                <Button
                                                    size="small"
                                                    variant="outlined"
                                                    startIcon={<ViewIcon />}
                                                    onClick={() => window.open(`/project/${project.id}`, '_blank')}
                                                >
                                                    View Details
                                                </Button>
                                            </ListItem>
                                        ))}
                                </List>
                            ) : (
                                <Typography variant="body1" sx={{ color: 'black', textAlign: 'center' }}>
                                    No collaborative projects found
                                </Typography>
                            )}
                        </CardContent>
                    </Card>
                </TabPanel>

                {/* Reject Dialog */}
                <Dialog
                    open={openRejectDialog}
                    onClose={() => setOpenRejectDialog(false)}
                    maxWidth="sm"
                    fullWidth
                >
                    <DialogTitle sx={{ color: 'black' }}>Reject Project</DialogTitle>
                    <DialogContent>
                        <Typography variant="body2" sx={{ mb: 2, color: 'black' }}>
                            Are you sure you want to reject "{selectedProject?.title}"?
                        </Typography>
                        <TextField
                            fullWidth
                            multiline
                            rows={3}
                            label="Reason for rejection (optional)"
                            value={rejectReason}
                            onChange={(e) => setRejectReason(e.target.value)}
                            placeholder="Please provide a reason for rejection..."
                            sx={{
                                '& .MuiInputBase-root': {
                                    color: 'black',
                                    '& fieldset': { borderColor: 'black' }
                                },
                                '& .MuiInputLabel-root': {
                                    color: 'black'
                                }
                            }}
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={() => setOpenRejectDialog(false)} sx={{ color: 'black' }}>
                            Cancel
                        </Button>
                        <Button
                            onClick={() => handleRejectProject(selectedProject.id)}
                            color="error"
                            variant="contained"
                        >
                            Reject Project
                        </Button>
                    </DialogActions>
                </Dialog>

                {/* Delete Confirmation Dialog */}
                <Dialog
                    open={openDeleteDialog}
                    onClose={handleCancelDelete}
                    maxWidth="sm"
                    fullWidth
                >
                    <DialogTitle sx={{ 
                        color: 'black', 
                        backgroundColor: '#fff3cd',
                        display: 'flex',
                        alignItems: 'center',
                        gap: 1
                    }}>
                        <DeleteIcon sx={{ color: '#856404' }} />
                        Confirm Delete
                    </DialogTitle>
                    <DialogContent sx={{ backgroundColor: '#fff3cd', pt: 2 }}>
                        <Typography variant="body1" sx={{ color: 'black', mb: 2 }}>
                            Are you sure you want to delete the project "{selectedProject?.title}"?
                        </Typography>
                        <Typography variant="body2" sx={{ color: '#856404' }}>
                            This action cannot be undone. The project and all its data will be permanently removed.
                        </Typography>
                    </DialogContent>
                    <DialogActions sx={{ backgroundColor: '#fff3cd', px: 3, pb: 2 }}>
                        <Button 
                            onClick={handleCancelDelete}
                            sx={{ 
                                color: 'black',
                                borderColor: 'black',
                                '&:hover': {
                                    borderColor: 'black',
                                    backgroundColor: 'rgba(0, 0, 0, 0.04)'
                                }
                            }}
                        >
                            Cancel
                        </Button>
                        <Button 
                            onClick={handleConfirmDelete}
                            variant="contained"
                            color="error"
                            startIcon={<DeleteIcon />}
                            sx={{
                                backgroundColor: '#dc3545',
                                '&:hover': {
                                    backgroundColor: '#c82333'
                                }
                            }}
                        >
                            Delete Project
                        </Button>
                    </DialogActions>
                </Dialog>

                <Snackbar
                    open={snackbar.open}
                    autoHideDuration={6000}
                    onClose={() => setSnackbar({ ...snackbar, open: false })}
                >
                    <Alert
                        onClose={() => setSnackbar({ ...snackbar, open: false })}
                        severity={snackbar.severity}
                        sx={{ width: '100%' }}
                    >
                        {snackbar.message}
                    </Alert>
                </Snackbar>
            </Container>
        </>
    );
};

export default AdminProjectManagement; 